import { Component, Input, Output, EventEmitter, SimpleChange, OnChanges } from '@angular/core';

@Component({
  selector: 'ng2-collapse',
  styles: [require('./ng2-collapse.component.scss')],
  template: require('./ng2-collapse.component.html'),
})
export class Ng2CollapseComponent implements OnChanges {
  @Input() public collapsed: boolean = false;
  @Input() public title: string;

  @Output() public collapse = new EventEmitter<any>();
  @Output() public expand = new EventEmitter<any>();

  ngOnChanges(changes: { [propName: string]: SimpleChange }) {
    if (changes['collapsed']) {
      this.collapsed ? this.hide() : this.show();
    }
  }

  toggle() {
    this.collapsed = !this.collapsed;

    this.collapsed ? this.hide() : this.show();
  }

  private hide() {
    this.collapse.emit({
      collapsed: this.collapsed,
    });
  }

  private show() {
    this.expand.emit({
      collapsed: this.collapsed,
    });
  }
}



