import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';

import { NG2_COLLAPSE_DIRECTIVES } from './ng2-collapse.directives';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
  ],
  declarations: [
    ...NG2_COLLAPSE_DIRECTIVES,
  ],
  exports: [
    ...NG2_COLLAPSE_DIRECTIVES,
  ],
})
export class Ng2CollapseModule { }



